/* SPAN elements with the classes below are added by prettyprint. */
.pln {
  color: #9c9ebb;
} /* plain text */
@media screen {
  .str {
    color: #2b81ff !important;
  } /* string content */
  .kwd {
    color: #008 !important;
  } /* a keyword */
  .com {
    color: #066 !important;
  } /* a comment */
  .typ {
    color: #f93636 !important;
  } /* a type name */
  .lit {
    color: #066 !important;
  } /* a literal value */
  /* punctuation, lisp open bracket, lisp close bracket */
  .pun, .opn, .clo {
    color: #660;
  }
  .tag {
    color: #00bcd4 !important;
  } /* a markup tag name */
  .atn {
    color: #f93636 !important;
  } /* a markup attribute name */
  .atv {
    color: #2b81ff !important;
  } /* a markup attribute value */
  .dec, .var {
    color: #f93636 !important;
  } /* a declaration; a variable name */
  .fun {
    color: red;
  } /* a function name */
}
/* Use higher contrast and text-weight for printable form. */
@media print, projection {
  .str {
    color: #060;
  }
  .kwd {
    color: #00bcd4;
    font-weight: bold;
  }
  .com {
    color: #600;
    font-style: italic;
  }
  .typ {
    color: #404;
    font-weight: bold;
  }
  .lit {
    color: #044;
  }
  .pun, .opn, .clo {
    color: #440;
  }
  .tag {
    color: #00bcd4;
    font-weight: bold;
  }
  .atn {
    color: #404;
  }
  .atv {
    color: #060;
  }
}
/* Put a border around prettyprinted code snippets. */
