ul.steppedprogress {
  counter-reset: step;
  display: flex;
  margin: auto;
  overflow: hidden;
  padding: 0;
  position: relative;
  text-align: center;
  z-index: 1;
}
ul.steppedprogress li {
  list-style-type: none;
  color: #303e67;
  line-height: normal;
  text-transform: uppercase;
  flex: 1;
  font-size: 12px;
  position: relative;
  padding: 0 2%;
  font-weight: 500;
  font-family: "Roboto", sans-serif;
}
ul.steppedprogress li:before {
  content: counter(step);
  counter-increment: step;
  width: 24px;
  height: 24px;
  line-height: 20px;
  display: block;
  font-size: 12px;
  color: #303e67;
  background: #f1f5fa;
  border-radius: 50%;
  margin: 0 auto 10px;
  position: relative;
  z-index: 1;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1.25px #f1f5fa;
}
ul.steppedprogress li:after {
  content: "";
  width: 100%;
  border: 0.5px dashed #eceff5;
  position: absolute;
  left: -50%;
  top: 12px;
  z-index: -1;
}
ul.steppedprogress li.complete:before {
  background: #7081b9;
  color: #ffffff;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1.25px #7081b9;
}
ul.steppedprogress li.complete.continuous:before {
  background: #0b51b7;
  color: #ffffff;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1.25px #0b51b7;
}
ul.steppedprogress li.complete.continuous span {
  color: #0b51b7;
}
ul.steppedprogress li.complete.finish:before {
  background: #ef4d56;
  color: #ffffff;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1.25px #ef4d56;
  content: "\f12c";
  font: normal normal normal 24px/1 "Material Design Icons";
  font-size: 16px;
  line-height: 20px;
}
ul.steppedprogress li.complete.finish span {
  color: #ef4d56;
}
ul.steppedprogress li.complete:after {
  border-color: #dfe4ee;
}
ul.steppedprogress li:first-child:after {
  content: none;
}

@media (max-width: 480px) {
  ul.steppedprogress {
    display: block;
  }
  ul.steppedprogress li {
    flex: none;
    clear: both;
    text-align: left;
    padding: 0;
    margin-left: 2px;
    min-height: 2.2em;
  }
  ul.steppedprogress li span {
    white-space: nowrap;
  }
  ul.steppedprogress li:before {
    float: none;
    display: inline-block;
    margin-right: 10px;
    text-align: center;
    margin-left: 0;
  }
  ul.steppedprogress li:after {
    content: "";
    width: 6px;
    height: 100%;
    position: absolute;
    left: 12px;
    top: -50%;
    z-index: -1;
  }
}
